var React=require("react/addons");
var $ = require('jquery');
var Modal=require("./modal");
var Button=require("../html/button");

/**
 * <Button btnName="MessageBox" doAction={MessageBox.show.bind(null,"i am title","i am message")} cssClass="btn-info" />
 * !!,show!!
 * show1:null
 * show2(String):
 * show3(String):
 */
var MessageBox = React.createClass({
	statics:{
		title : null,
		message : null,
		show : function(title,message){
			mb = (<Modal id="MessageBoxModal" title={title} submitAction={Modal.hide.bind(null,"MessageBoxModal")} >
			        <span>{message}</span> <br/>
					<br/>
			      </Modal>);
			$("body").append("<div id='MessageBox'></div>");
			React.render(mb, document.getElementById("MessageBox")); /*$("#"+this.props.uniqueID), */
			$("#MessageBoxModal").modal();
		}
	},
	render : function(){
		return (<div></div>);
	}
});

module.exports = MessageBox;


