var React = require('react/addons');/** *  *  <PageSizeSelect onselect={this._selectPageSize} /> *  onselectparent */var PageSizeSelect=React.createClass({    _selectPageSize:function(e){        if(e.target&& e.target.value>0){            this.props.onselect(e.target.value);        }    },    _setPageNum:function(e){        if(e.keyCode === 13){            e.preventDefault();            if(e.target.value>0){                this.props._setPageNum(e.target.value);            }        }    },    render:function(){      var select=<b className="page-size-left"><input type="text" className="form-control input-xs" onKeyUp={this._setPageNum}/><select className="form-control input-nm" onChange={this._selectPageSize}><option value="10">10</option><option value="15">15</option><option value="20">20</option><option value="30">30</option><option value="40">40</option><option value="50">50</option><option value="100">100</option></select>&nbsp;</b>;      return (select);    }});module.exports = PageSizeSelect;


