var React = require('react/addons');
/**
 * <Button actions=[] code="" btnName="" disabled={this.state.dis} disabledName="......" doAction={this._doAction}/>
 * disabled:disabledtrue
 * disabledName
 * doAction:parent
 * id:id, DOM, 
 * cssClass(String):,Bootstrap,:
    btn-default : ;
    btn-primary : , ,;
    btn-success : , ;
    btn-info    : , ;
    btn-warning : , ;
    btn-danger  : , ;
    btn-lg      : ;
    btn-sm      : ;
    btn-xs      : ;
    btn-block   : ;
    active      : .
    , :"btn-danger btn-xs", , Bootstrap.
 * customClass(String): style, stylecss/style.css
 * actions:
 * code:
 * @type {*|Function}
 */
var Button=React.createClass({
    displayName:'Common Button',

    getInitialState:function(){
        return {
            dis:false,
            cssClass:'primary'
        };
    },
    _doClick:function(e){ //e is short for event
      e.preventDefault()
      if(this.props.doAction){
          this.props.doAction();
      }
    },
    getDefaultProps: function(){
        return{
            type:"button",
        }
    },
    render:function(){
        var name="";
        var dis = this.props.disabled;
        var cssClass = this.props.cssClass?this.props.cssClass:'btn-primary';
        var className = this.props.customClass? this.props.customClass: "btn "+cssClass
        if(dis&&dis==true){
            return <button type={this.props.type} className={className} >{this.props.disabledName}</button>; //cannot use class=XX , use className=XX instead
        }
        if(this.props.btnName){
            name=this.props.btnName
        };
        var actions=this.props.actions;//
        var code=this.props.code;//
        //var isDisplay=false;//
        var isDisplay=true;//
        if(actions){
            if(code){
                var l=actions.length;
                for(var i=0;i<l;i++){
                    var obj=actions[i];
                    if(obj==code){
                        isDisplay=true;
                        break;
                    }
                }
            }
        };

    return (isDisplay?<button type={this.props.type} className={className} onClick={this._doClick}>{name}</button>:null);
    }
});
module.exports=Button;



